<template>
  <div class="section">
    <table >
        <tr colspan="10">
          <td colspan="10">实钻信息</td>
        </tr>
        <tr>
          <td>发布时间</td>
          <td colspan="2">{{list.start_at}}</td>
          <td>专家</td>
          <td colspan="2">{{list.expert}}</td>
          <td>联系方式</td>
          <td colspan="3">{{list.mobile}}</td>
        </tr>
        <tr>
          <td>设计井深</td>
          <td>{{list.design_depth}}</td>
          <td>当前井深</td>
          <td>{{list.current_depth}}</td>
          <td>井型</td>
          <td>{{list.well_shape}}</td>
          <td>井别</td>
          <td>{{list.well_type}}</td>
          <td>施工队伍</td>
          <td>{{list.operate_unit}}</td>
        </tr>
        <tr>
          <td>钻压</td>
          <td>{{list.wob}}</td>
          <td>转速</td>
          <td>{{list.rpm}}</td>
          <td>泵压</td>
          <td>{{list.pump}}</td>
          <td>排量</td>
          <td>{{list.flow}}</td>
          <td>机械转速</td>
          <td>{{list.rop}}</td>
        </tr>
        <tr>
          <td>钻头类型</td>
          <td>{{list.bit_type}}</td>
          <td>钻头型号</td>
          <td>{{list.bit_model}}</td>
          <td>水眼</td>
          <td>{{list.water_hole}}</td>
          <td>纯钻时间</td>
          <td>{{list.drilling_time}}</td>
          <td>钻头进尺</td>
          <td>{{list.bit_footage}}</td>
        </tr>
        <tr>
          <td>地层</td>
          <td>{{list.formation}}</td>
          <td>岩性</td>
          <td>{{list.lithology}}</td>
          <td>全烃</td>
          <td>{{list.totalgas}}</td>
          <td>油气显示</td>
          <td>{{list.oil_show}}</td>
          <td>日进尺</td>
          <td>{{list.daily_footage}}</td>
        </tr>
        <tr>
          <td>钻具组合</td>
          <td colspan="9">{{list.bha}}</td>
        </tr>
        <tr>
          <td>泥浆性能</td>
          <td>密度</td>
          <td>粘度</td>
          <td>失水</td>
          <td>含砂</td>
          <td>PH值</td>
          <td>泥饼厚度</td>
          <td>n</td>
          <td>k</td>
          <td>摩阻系数</td>
        </tr>
        <tr>
          <td>当前工况</td>
          <td colspan="9">{{list.condition}}</td>
        </tr>
        <tr>
          <td colspan="10">基础信息(数据提取)</td>
        </tr>
        <tr>
          <td rowspan="2">存在问题</td>
          <td>本井</td>
          <td colspan="8">{{list.problem_this}}</td>
        </tr>
        <tr>
          <td>邻井</td>
          <td colspan="8">{{list.problem_adjacent}}</td>
        <tr>
          <td colspan="10">技术措施与建议</td>
        </tr>
        <tr>
          <td>工程措施</td>
          <td colspan="9">{{list.suggestion}}</td>
        </tr>
        <tr>
          <td>钻井液</td>
          <td colspan="9"></td>
        </tr>
    </table>
  </div>
</template>

<script>
  import request from "@/utils/request";
  export default {
    props: ["route"],
    data() {
      return {
        current: 1,
        list: {}
      }
    },
    mounted: function () {
      let params = this.route;
      request({
        url: "/support/remotesupport",
        method: "get",
        params
      }).then(res => {
        this.list = res;
      });
    },
    methods:  {
  
    }
  } 
</script>



